import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import VueRouter from 'vue-router'
import Select_page from "@/components/Select_page";
import Question_main_page from "@/components/Question_main_page";

Vue.config.productionTip = false

Vue.use(ElementUI)
Vue.use(VueRouter)

const router = new VueRouter({
    routes: [{
        path: "/", component: Select_page
    }, {
        path: "/question_main", name: "question_main_page", component: Question_main_page
    }]
});

new Vue({
    render: h => h(App),
    router,
}).$mount('#app')
